
import { defineComponent } from 'vue';
import {
  withAnimationClasses,
  withAnimationProps,
} from '../../mixins/animations';

export default defineComponent({
  props: {
    ...withAnimationProps(),
    size: String,
    uppercase: Boolean,
  },
  setup(props) {
    return {
      ...withAnimationClasses(props),
    };
  },
});
