
import { defineComponent } from 'vue';
import { withTooltipClasses, withTooltipProps } from '../../mixins/tooltip';

export default defineComponent({
  inheritAttrs: false,
  props: {
    ...withTooltipProps(),
    selected: Boolean,
  },
  setup(props) {
    return {
      ...withTooltipClasses(props),
    };
  },
});
